.nav-main {
    display: none;
    clear: both;
    background: $color-white;
  
    a {
      position: relative;
      text-decoration: none;
      padding-bottom: 2px;
      transition: 0.3s;
      display: block;
      width: 100%;
      padding: 20px 0;
  
      &::before {
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        height: 4px;
        width: 100%;
        background-color: $color-secondary;
        transform: scale(0, 1);
        transform-origin: 0 50%;
        transition: 0.3s;
      }
  
      &:hover {
        color: $color-secondary;
        font-weight: bold;
  
        &::before {
          transform: scale(1, 1);
        }
      }
    }
  }
  
  .nav-toggle {
    display: inline-block;
    //float: right;
    width: 40px;
    height: 40px;
    cursor: pointer;
    transition: all 0.5s ease;
  
    div {
      display: block;
      width: 40px;
      height: 3px;
      margin-top: 7px;
      background-color: $color-primary;
      transition: all 0.5s;
    }
    &:hover {
      opacity: 0.7;
    }
  }
  